* {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Inter', 'Sans Serif';
}

nav {
  background: #f55a5a;
  height: 55px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
}

nav > img {
  height: 24px;
  width: 24px;
}

nav > h1 {
  font-weight: normal;
  font-size: 15px;
  margin-left: 7px;
  font-size: 14.4608px;
  letter-spacing: -0.055em;
  color: #ffffff;
}

.hero {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.card {
  display: flex;
  flex-direction: row;
  margin-top: 8px;
  padding-bottom: 8px;
  margin-left: 20px;
  margin-right: 20px;
  border-bottom: 1px solid #e6e6e6;
  max-width: 500px;
}

.card > img {
  height: 168px;
  width: 125px;
  padding: 5px;
  border-radius: 10px;
  object-fit: cover;
}

.card--detail {
  display: flex;
  flex-direction: column;
  padding-top: 6px;
  margin: 5px;
  color: #2b283a;
  margin-left: 15px;
}

.card--detail > img {
  height: 5px;
  width: 5px;
}

.card--detail > h2 {
  font-size: 15px;
  color: #2b283a;
}

.card--detail > h3 {
  margin-top: 5px;
  font-size: 10px;
}

.card--detail > p {
  margin-top: 10px;
  font-size: 11px;
  font-weight: 400;
  line-height: 15px;
  color: #2b283a;
}

.card--detail_inline {
  display: flex;
  flex-direction: row;
  font-size: 10px;
  margin-bottom: 5px;
}

.card--detail_inline > img {
  margin-right: 6px;
  height: 10px;
  width: auto;
}

.card--detail_inline > p {
  margin-right: 10px;
  font-size: 10.24px;
  letter-spacing: 0.17em;
}

.card--detail_inline > a {
  font-size: 10.24px;
  color: #918e9b;
}

@media only screen and (min-width: 700px) {
  * {
    margin: 0;
    padding: 0;
  }

  body {
    font-family: 'Inter', 'Sans Serif';
    background: #f0f0f0;
  }

  nav {
    background: #f55a5a;
    height: 65px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
  }

  nav > img {
    height: 26px;
    width: 26px;
  }

  nav > h1 {
    font-weight: normal;
    margin-left: 10px;
    font-size: 20px;
    letter-spacing: -0.055em;
    color: #ffffff;
  }

  .hero {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .card {
    display: flex;
    flex-direction: row;
    margin-top: 15px;
    padding-bottom: 10px;
    margin-left: 20px;
    margin-right: 20px;
    border-bottom: 1px solid #e6e6e6;
    max-width: 700px;
  }



  .card--img_container > img {
    height: 300px;
    width: 250px;
    padding: 5px;
    border-radius: 10px;
    object-fit: cover;
  }

  .card--detail {
    display: flex;
    flex-direction: column;
    padding-top: 20px;
    margin: 5px;
    color: #2b283a;
    margin-left: 15px;
  }

  .card--detail > h2 {
    font-size: 25px;
    color: #2b283a;
  }

  .card--detail > h3 {
    margin-top: 40px;
    font-size: 15px;
  }

  .card--detail > p {
    margin-top: 10px;
    font-size: 16px;
    font-weight: 300;
    line-height: 22px;
    color: #2b283a;
  }

  .card--detail_inline {
    display: flex;
    flex-direction: row;
    align-items: center;
    font-size: 15px;
    margin-bottom: 5px;
  }

  .card--detail_inline > img {
    margin-right: 6px;
    height: 15px;
    width: auto;
  }

  .card--detail_inline > p {
    margin-right: 10px;
    font-size: 15px;
    letter-spacing: 0.17em;
  }

  .card--detail_inline > a {
    font-size: 15px;
    color: #918e9b;
  }
}
